  hidePublishDialog()
  wx.showToast({ title: 'ÂèëÂ∏ÉÊàêÂäü', icon: 'success' })
  //Âà∑Êñ∞ÂèëÂ∏ÉÂàóË°®Êï∞ÊçÆ
