    tags: newDiary.tags//newDiary.tags.split(',').map(tag => tag.trim()).filter(tag => tag)
  }
  // const params =  
  // {
  //   id: '2',
  //   date: '2024-01-14',
  //   title: 'Âπ≥ÈùôÁöÑÊÄùËÄÉ',
  //   content: '‰ªäÂ§©Â∑•‰ΩúÊØîËæÉÂøôÔºå‰ΩÜÊôö‰∏äÊúâÊó∂Èó¥Èùô‰∏ãÊù•ÊÄùËÄÉ‰∏Ä‰∫õ‰∫ãÊÉÖ„ÄÇÁîüÊ¥ªÈúÄË¶ÅÂπ≥Ë°°„ÄÇ',
