// ‰∫§‰∫íÊìç‰Ωú
const toggleLike = (postId) => {
  const post = publicPosts.value.find(p => p.id === postId)
  if (post) {
    post.isLiked = !post.isLiked
    post.likes += post.isLiked ? 1 : -1
  }
}

const toggleSchedule = async(scheduleId) => {
  try {
    // Êü•ÊâæÂØπÂ∫îÁöÑË°åÁ®ãÈ°π
    const item = todaySchedule.value.find(s => s.id === scheduleId);
    if (!item) {
      console.error('Êú™ÊâæÂà∞ÂØπÂ∫îÁöÑË°åÁ®ãÈ°π');
      uni.showToast({ title: 'Êï∞ÊçÆÂºÇÂ∏∏', icon: 'none' });
      return;
    }

    // ‰øùÂ≠òÂéüÂßãÁä∂ÊÄÅÁî®‰∫éÈîôËØØÂõûÊªö
    const originalCompleted = item.completed;
    // ÊòæÁ§∫Âä†ËΩΩÁä∂ÊÄÅ
    item.loading = true;

    // ÂÖ≥ÈîÆ‰øÆÊîπÔºöÂêéÁ´ØÊé•Âè£Ë∑ØÂæÑÂåÖÂê´/completeÂêéÁºÄ
    const res = await request.put(`/api/v1/life/schedules/${scheduleId}/complete`);

    // Â§ÑÁêÜÂìçÂ∫î
    if (res && res.code === 200) {
      // ‰ΩøÁî®ÂêéÁ´ØËøîÂõûÁöÑÁä∂ÊÄÅÊõ¥Êñ∞Êú¨Âú∞
      item.completed = res.data.completed;
      uni.showToast({ title: 'Êõ¥Êñ∞ÊàêÂäü', icon: 'success' });
    } else {
      // ÂìçÂ∫îÂºÇÂ∏∏Êó∂ÂõûÊªöÁä∂ÊÄÅ
      item.completed = originalCompleted;
      uni.showToast({ 
        title: res?.message || 'Êõ¥Êñ∞Â§±Ë¥•', 
        icon: 'none' 
      });
    }
  } catch (error) {
    // ÁΩëÁªúÈîôËØØÂ§ÑÁêÜ
    const item = todaySchedule.value.find(s => s.id === scheduleId);
    if (item) {
      // ÂõûÊªöÁä∂ÊÄÅ
      item.completed = !item.completed;
      item.loading = false;
    }
    
    // ÈîôËØØÊèêÁ§∫ÁªÜÂåñ
    let errorMsg = 'ÁΩëÁªúÂºÇÂ∏∏ÔºåËØ∑Á®çÂêéÈáçËØï';
    if (error.message.includes('404')) {
      errorMsg = 'Êé•Âè£‰∏çÂ≠òÂú®';
    } else if (error.message.includes('401')) {
      errorMsg = 'ËØ∑ÂÖàÁôªÂΩï';
    } else if (error.message.includes('500')) {
      errorMsg = 'ÊúçÂä°Âô®ÈîôËØØ';
    }
    
    uni.showToast({ title: errorMsg, icon: 'none' });
    console.error('ÂàáÊç¢Ë°åÁ®ãÁä∂ÊÄÅÂ§±Ë¥•:', error);
  } finally {
    // Á°Æ‰øùÂä†ËΩΩÁä∂ÊÄÅÂÖ≥Èó≠
    const item = todaySchedule.value.find(s => s.id === scheduleId);
    if (item) {
      item.loading = false;
    }
  }
};
    
const getMoodEmoji = (mood) => {
